
import { INITIATE_INVENTORY_SETUP, UPDATE_INVENTORY_VALUE } from "../types";

export const initiateInventorySetUp = async (propertyId = "") => {

  try {
    const inventorySetUp = {
      propertyId,
      structureType: "FLATWISE", // ROOMWISE, FLATWISE
      rooms: true,
      beds: true,
      isBlocks: true,
      numberOfBlocks: null,
      blockNameFormat: "NUMERIC",  //  ALPHANUMERIC, CUSTOM, ALPHABETIC, NUMERIC
      sequenceFrom: "",
      sequenceTo: "",
      buildingName:"",
      isDraft: true,
      shortened: true,
      tabStatus: "PENDING",  // PENDING, COMPLETED, INPROGRESS
      floorsPerBlock: [
        {
          block: "",
          numberOfFloors: "",
          shortenedName: "",
          blockName:""
        }
      ],
      flatsPerFloor: [
        {
          block: "",
          floor: "",
          flats: ""
        }
      ]
    }

    return {
      type: INITIATE_INVENTORY_SETUP,
      payload: inventorySetUp,
    };
  } catch (error) {
    console.log("error", error)
  }

};

export const updateInventorySetUp = (name, value) => {
  return {
    type: UPDATE_INVENTORY_VALUE,
    payload: { name, value },
  };
};
